<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="'薪資條'" :routeTarget="{ name: 'Report' }"></C-Back-Nav>

    <section class="relative z-0 mt-[52px] p-3">
      <div class="center my-4 flex justify-between">
        <div>
          <p><strong>設計師：</strong> {{ designerName + " | " + GETTER_MECHANTNAME }}</p>
        </div>
      </div>
      <div class="center flex flex-wrap gap-2">
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="lastMonth"
        >
          上個月
        </button>
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="thisMonth"
        >
          本月
        </button>
        <month-picker-input
          lang="zh"
          :input-pre-filled="true"
          :default-month="+this.$dayjs().month() + 1"
          :default-year="+$dayjs(new Date()).format('YYYY')"
          v-model="date"
        />
      </div>
      <div v-if="isDataEmpty" class="font-semibold my-5">尚無資料</div>
      <div v-else>
        <div class="border-b flex-wrap py-4 text-left text-sm flex bg-[#fff]/80 border-solid">
          <div
            class="flex w-4/12 md:w-2/12 mb-2"
            v-for="(item, index) in increaseItem"
            :key="index"
          >
            <div class="font-semibold w-[100px] pl-1">{{ item.itemName }}</div>
            <div class="text-left w-[60px] pl-2">{{ item.price }}</div>
          </div>
        </div>

        <div class="border-b flex-wrap py-4 text-left text-sm flex bg-[#fff]/80 border-solid">
          <div
            class="flex w-4/12 md:w-2/12 mb-2"
            v-for="(item, index) in decreaseItem"
            :key="index"
          >
            <div class="font-semibold w-[100px] pl-1">{{ item.itemName }}</div>
            <div class="text-left w-[60px] pl-2">{{ item.price }}</div>
          </div>
        </div>
        <div class="flex-wrap my-5 text-left text-sm flex bg-[#fff]/80">
          <div class="w-4/12 md:w-[14.285714%] mb-3 pl-1">
            <div class="font-semibold w-full mb-2">實領薪資</div>
            <div class="w-full">{{ summary }}</div>
          </div>
          <div class="w-4/12 md:w-[14.285714%] mb-3 pl-1">
            <div class="font-semibold w-full mb-2">互助獲得(點)</div>
            <div class="w-full">{{ helperPointRetrieve }}</div>
          </div>
          <div class="w-4/12 md:w-[14.285714%] mb-3 pl-1">
            <div class="font-semibold w-full mb-2">指定實業績</div>
            <div class="w-full">{{ designateTruthPerformance }}</div>
          </div>
          <div class="w-4/12 md:w-[14.285714%] mb-3 pl-1">
            <div class="font-semibold w-full mb-2">不指定實業績</div>
            <div class="w-full">{{ nonDesignateTruthPerformance }}</div>
          </div>
          <div class="w-4/12 md:w-[14.285714%] mb-3 pl-1">
            <div class="font-semibold w-full mb-2">特抽總業績</div>
            <div class="w-full">{{ specialTruthPerformance }}</div>
          </div>
          <div class="w-4/12 md:w-[14.285714%] mb-3 pl-1">
            <div class="font-semibold w-full mb-2">總實業績</div>
            <div class="w-full">{{ truthPerformance }}</div>
          </div>
          <div class="w-4/12 md:w-[14.285714%] mb-3 pl-1">
            <div class="font-semibold w-full mb-2">備註</div>
            <div class="w-full">{{ remark }}</div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import { MonthPickerInput } from 'vue-month-picker';

export default {
  name: 'priceList-index',
  description: '薪資條',
  metaInfo: {
    title: 'BeautyOS 設計師',
    titleTemplate: '%s | 薪資條',
  },
  components: {
    MonthPickerInput,
  },
  data() {
    return {
      date: this.$dayjs().valueOf(),
      designerName: 'LinKen',
      increaseItem: [],
      decreaseItem: [],
      summary: 0,
      helperPointRetrieve: 0,
      designateTruthPerformance: 0,
      nonDesignateTruthPerformance: 0,
      specialTruthPerformance: 0,
      truthPerformance: 0,
      remark: '',
      isDataEmpty: true,
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    async init() {
      await this.getUserSalaryReport();
    },
    arrayEmpty(array) {
      return array.length === 0;
    },
    async getUserSalaryReport() {
      const input = {
        date: this.$dayjs(this.date.from).valueOf(),
        isCellphone: true,
      };
      const resp = await this.$api.userSalaryReport(input);
      const { data, errors } = JSON.parse(JSON.stringify(resp.data));
      if (errors) return;
      if (!this.arrayEmpty(data.userSalaryReport)) {
        this.isDataEmpty = false;
        this.setData(data.userSalaryReport[0]);
      } else {
        this.isDataEmpty = true;
      }
    },
    setData(data) {
      this.designerName = data.name;
      this.increaseItem = data.increaseItem;
      this.decreaseItem = data.decreaseItem;
      this.summary = data.summary;
      this.helperPointRetrieve = data.helperPointRetrieve;
      this.designateTruthPerformance = data.designateTruthPerformance;
      this.nonDesignateTruthPerformance = data.nonDesignateTruthPerformance;
      this.specialTruthPerformance = data.specialTruthPerformance;
      this.truthPerformance = data.truthPerformance;
      this.remark = data.remark;
    },
    // 本月
    thisMonth() {
      const dt = new Date(Date.now());
      const y = dt.getFullYear();
      const m = dt.getMonth() + 1;
      const firstDay = new Date(y, m - 1, 1);
      this.date = +firstDay;
    },
    // // 上個月
    lastMonth() {
      const dt = new Date(Date.now());
      const y = dt.getFullYear();
      const m = dt.getMonth();
      const firstDay = new Date(y, m - 1, 1);
      this.date = +firstDay;
    },
  },
  watch: {
    date() {
      this.getUserSalaryReport();
    },
  },
  computed: {
    ...mapState('userInfo', ['userInfo']),
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
  },
};
</script>

<style>
.month-picker__year button {
  width: 2.5rem !important;
}
</style>
